import { Node, Element, Text, Editor, Range } from 'slate'
import { IDomEditor, DomEditor } from '@wangeditor/core'
import { CustomElement } from '../../../../custom-types'

const COMMON_HOVERBAR_KEYS = {
  // key å³ element type
  link: {
    menuKeys: ['editLink', 'unLink', 'viewLink'],
  },
  image: {
    menuKeys: [
      'imageWidth30',
      'imageWidth50',
      'imageWidth100',
      'editImage',
      'viewImageLink',
      'deleteImage',
    ],
  },
  video: {
    menuKeys: ['deleteVideo'],
  },
  pre: {
    menuKeys: ['codeBlock', 'codeSelectLang'],
  },
  table: {
    menuKeys: [
      'tableHeader',
      'tableFullWidth',
      'insertTableRow',
      'deleteTableRow',
      'insertTableCol',
      'deleteTableCol',
      'deleteTable',
    ],
  },
}

export function genDefaultHoverbarKeys() {
  return {
    ...COMMON_HOVERBAR_KEYS,

    // ä¹å¯ä»¥èªå®ä¹ match æ¥å¹éåç´ ï¼æ­¤æ¶ key å°±éæäº
    text: {
      match: (editor: IDomEditor, n: Node) => {
        const { selection } = editor
        if (selection == null) return false // æ éåº
        if (Range.isCollapsed(selection)) return false // æªéä¸­æå­ï¼éåºçæ¯æå ç

        const selectedElems = DomEditor.getSelectedElems(editor)
        const notMatch = selectedElems.some((elem: CustomElement) => {
          if (editor.isVoid(elem)) return true

          const { type } = elem
          if (['pre', 'code'].includes(type)) return true
        })
        if (notMatch) return false

        if (Text.isText(n)) return true // å¹é text node
        return false
      },
      menuKeys: [
        'headerSelect',
        'insertLink',
        'bulletedList',
        '|',
        'bold',
        'through',
        'color',
        'bgColor',
        'clearStyle',
      ],
    },
    // other hover bar ...
  }
}

export function genSimpleHoverbarKeys() {
  return COMMON_HOVERBAR_KEYS
}
